import React from 'react';
import { View, Text, StyleSheet, Pressable, ScrollView } from 'react-native';

export default function RulesScreen({ navigation }) {

    return (
        <ScrollView contentContainerStyle={styles.container}>
            <Text style={styles.title}>Zasady gry</Text>
            <Text style={styles.text}>
                • Celem gry jest odgadnięcie ukrytego słowa, litera po literze.{'\n\n'}
                • Masz maksymalnie 6 pomyłek – każda błędna litera przybliża Cię do przegranej.{'\n\n'}
                • Wybieraj litery z klawiatury ekranowej.{'\n\n'}
                • Jeśli zgadniesz całe słowo – wygrywasz! Jeśli popełnisz 6 błędów – przegrywasz.
            </Text>

            <Pressable style={styles.button} onPress={() => navigation.goBack()}>
                <Text style={styles.buttonText}>Powrót</Text>
            </Pressable>
        </ScrollView>
    );
};

const styles = StyleSheet.create({
    container: {
        flexGrow: 1,
        padding: 24,
        justifyContent: 'center',
        alignItems: 'center',
        backgroundColor: '#fff',
    },
    title: {
        fontSize: 26,
        fontWeight: 'bold',
        marginBottom: 24,
    },
      text: {
        fontSize: 18,
        lineHeight: 26,
        marginBottom: 40,
        textAlign: 'left',
    },
    button: {
        paddingVertical: 12,
        backgroundColor: '#373F51',
        paddingHorizontal: 24,
        borderRadius: 8,
    },
    buttonText: {
        color: '#fff',
        fontSize: 18,
    },
});